/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography


p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

hr {
 border-top: 1px solid var(--global-divider-color);
}
.card-header {
 text-align: center;
 box-shadow: 2px;
 border-radius: 2px; 
 border-color: rgb(0, 58, 130);
 
}
.month-name {
 
 align-items: center;
 font-size: large;
 font-weight: bold;
 font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}



#accordion {
 background-color: azure;
}
table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

//	margin: 0px;
.mbtablestyle {
 border-collapse: collapse;

 > table {
	width: 100%;
	}
 > table, td, th {
 border: 2px solid #ced2d6;
 	font-size: 1rem;
	padding-top: 0.3rem;
 	padding-bottom: 0.3rem;
 	padding-left: 1rem;
 	padding-right: 2rem;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}


// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}


// Card

.card {
 background-color: var(--global-card-bg-color);
 

 img {
 width: 100%;
 // height: 100%;
 
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 
 // margin-bottom: 10px;
 .row {
 display: flex;
 align-items: center;
 
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
}
.profile.float-right{
 margin-left: 1rem;
}
.profile.float-left{
 margin-right: 1rem;
}

@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.9rem;
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}


// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;
 padding: 0;
 img {
 height: 3rem;
 
 }
}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration:none;
 }
 }
 .navbar-brand {
 color: var(--global-text-color);
 
 }
 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
}

// Social (bottom)

.social {
