/*******************************************************************************
 * Themes
 ******************************************************************************/
 
:root {
 --global-bg-color: #{$white-color};
 --global-code-bg-color: #{$code-bg-color-light};
 --global-text-color: #{$black-color};
 --global-text-color-light: #{$grey-color};
 --global-theme-color: #{$blue-color-dark};
 --global-hover-color: #{$purple-color};
 --global-footer-bg-color: #{$grey-color-dark};
 --global-footer-text-color: #{$grey-color-light};
 --global-footer-link-color: #{$white-color};
 --global-distill-app-color: #{$grey-color};
 --global-divider-color: rgba(0,0,0,.1);
 --global-card-bg-color: #{$white-color};
 --pub-article: #{$blue-color-dark};
 --pub-inproceedings: #{$purple-color};
 --pub-misc: #{$black-color};
 --newsletter-border-color: rgb(44, 44, 44);
 .profile_image_class {
 display: none;

 

 }
 .profile_dark_image_class {
 display: block;
 position: relative;
 padding-top: 5px;
 
 
 
 
 }

 .fa-sun {
 display : none;
 }
 .fa-moon {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }
 
 .repo-img-light {
 display: block;
 }
 .repo-img-dark {
 display: none;
 }
 
}


html[data-theme='dark'] {
 --global-bg-color: #{$grey-color-dark};
 --global-code-bg-color: #{$code-bg-color-dark};
 --global-text-color: #{$grey-color-light};
 --global-text-color-light: #{$grey-color-light};
 --global-theme-color: #{$cyan-color};
 --global-hover-color: #{$cyan-color};
 --global-footer-bg-color: #{$grey-color-light};
 --global-footer-text-color: #{$grey-color-dark};
 --global-footer-link-color: #{$black-color};
 --global-distill-app-color: #{$grey-color-light};
 --global-divider-color: #424246;
 --global-card-bg-color: #{$grey-900};
 --pub-article: #{$cyan-color};
 --pub-inproceedings: #{$purple-color};
 --pub-misc: #{$grey-color-light};
 --newsletter-border-color: rgb(138, 138, 138);

 .fa-sun {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }
 .fa-moon {
 display : none;
 }
 

 .profile_dark_image_class {
 display: none;

 }
 .profile_image_class {
 display: inline-block;
 position: relative;
 padding-top: 5px;
 
 

 
 }
 .repo-img-light {
 display: none;
 }
 .repo-img-dark {
 display: block;
 }
}

